
#verse-header {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    
}

#verse-header h1 {
    font-size: 2rem;
    margin-bottom: 40px;
    color: #333;
}

#verse-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 230px;
    /* background-color: #f0f0f0; */
}

#verse {
    background-color: #d3d3d3;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
    max-width: 600px;
    width: 100%;
}